import React, { Component } from 'react';
import './SideNav.css'
import './w3.css'
import logo from '../assets/logo.png'
import { Link, withRouter } from 'react-router-dom'

class SideNav extends Component {
    state = {
        isClicked: false
    }

    w3_open = () => {
        document.getElementsByClassName("w3-sidenav")[0].style.display = "block";
        this.setState({
            isClicked: true
        })
    }
    w3_close = () => {
        document.getElementsByClassName("w3-sidenav")[0].style.display = "none";
        this.setState({
            isClicked: false
        })
    }

    render() {
        const { history } = this.props;
        return (
            <div>
                <nav className={`w3-sidenav w3-collapse sideBar ${isActive(history, "/")} `} style={{ height: window.innerHeight, position: "absolute" }} >
                    <a href="javascript:void(0)" onClick={() => this.w3_close()} className="w3-closenav w3-large w3-hide-large">Close &times;</a>
                    <Link to="/user/account" onClick={() => this.w3_close()} className={`w3-bar-item w3-button  ${isActive(history, "/user/account")}`}>Account</Link>
                    <Link to="/user/downloadApp" onClick={() => this.w3_close()} className={`w3-bar-item w3-button  ${isActive(history, "/user/downloadApp")}`}>Download app</Link>
                    <Link to="/user/subscription" onClick={() => this.w3_close()} className={`w3-bar-item w3-button  ${isActive(history, "/user/subscription")}`}>Subscription</Link>
                    <Link to="/user/billingCardInfo" onClick={() => this.w3_close()} className={`w3-bar-item w3-button  ${isActive(history, "/user/billingCardInfo")} ${isActive(history, "/user/billingInfo")}`}>Billing</Link>
                    <Link to="/user/pricingPlan" onClick={() => this.w3_close()} className={`w3-bar-item w3-button  ${isActive(history, "/user/pricingPlan")}`}>Pricing plan</Link>
                    <Link to="/user/logout" className={`w3-bar-item w3-button  ${isActive(history, "/user/logout")}`}>Logout</Link>
                </nav>

                <div className="w3-main" style={{ marginLeft: "200px" }}>
                    <header>
                        {
                            (this.state.isClicked) ? (
                                <span className="w3-opennav w3-xlarge w3-hide-large" onClick={() => this.w3_close()}>&#9776;</span>
                            ) : (
                                    <span className="w3-opennav w3-xlarge w3-hide-large" onClick={() => this.w3_open()}>&#9776;</span>
                                )
                        }


                    </header>
                </div>
            </div>

        )
    }

}

export const isActive = (history, path) => {
    if (history.location.pathname === path) {
        return "active";
    }
    // console.log (history,path)
};

export default withRouter(SideNav);
